<template>
  <div class="pageContent">
    <el-card shadow="always">
      <div class="pageHead">不可用余额明细</div>
      <el-table :data="list" style="width: 100%">
        <el-table-column label="业务类型">
          <template slot-scope="scope">
            <div v-if="scope.row.type == 0">退款</div>
            <div v-if="scope.row.type == 1">提现</div>
          </template>
        </el-table-column>
        <el-table-column prop="businessNo" label="业务单号"></el-table-column>
        <el-table-column prop="amount" label="金额"></el-table-column>
        <el-table-column prop="createTime" label="交易发生时间"></el-table-column>
        <el-table-column label="当前状态">
          <template slot-scope="scope">
            <div v-if="scope.row.state == 0">冻结中</div>
            <div v-if="scope.row.state == 1">已解冻</div>
          </template>
        </el-table-column>
        <el-table-column prop="description" label="说明"></el-table-column>
      </el-table>
      <div class="flex-row-end" style="padding-top:20px;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5,10,15,20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopAssetFreezeRecordList } from '@/api/assetsManage/account'
export default {
  data(){
    return{
      searchList:{
        pageSize:5,
        pageNum:1
      },
      list:[],
      listTotal:0
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData()
  },
  methods:{
    getData(){
      getShopAssetFreezeRecordList(this.searchList).then(res=>{
        console.log('不可用余额明细列表',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    // 分页
    handleSizeChange(val) {
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      this.searchList.pageNum = val
      this.getData();
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>